:root {
  --primary: hsl(217,91%,5%);
  --accent: hsl(277,89%,40%);
  --text: hsl(0, 0%, 23%);
  --headings: hsl(0, 0%, 89%);
  --primary-two: hsla(217, 92%, 5%, 0.9);
  --button: hsl(277, 85%, 30%);
}

:root {
  --montserrat: "Montserrat", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--montserrat);
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.navbar {
  background-color: var(--primary);
  font-family: var(--montserrat);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  height: 57px;
}
.navbar .brand-logo {
  max-width: 21.25rem;
}
.navbar p {
  color: var(--headings);
  font-size: 1.25rem;
  text-transform: uppercase;
  font-weight: 600;
  font-style: normal;
}
.navbar p span {
  color: var(--accent);
  font-size: 1.5rem;
  letter-spacing: 1px;
}
@media (max-width: 39.9375em) {
  .navbar p {
    display: none;
  }
}

.hero-section {
  background-image: url("/coverphoto.jpg");
  background-size: cover;
  background-position: center;
  height: 100vh;
}
.hero-section .row {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  /*.col-2{
      text-align: center;
      background-color: var(--primary);
      height: 48rem;
      width: 30%;
      max-width: 650px;
      margin: 2rem 10rem 0 0;
      padding: 1rem;
      color: var(--headings);
      border-radius: 20px;*/
}
.hero-section .row .col-1 {
  color: var(--headings);
  text-align: left;
}
.hero-section .row .col-1 h1 {
  font-size: 2.25rem;
  font-weight: 700;
  margin: 0;
  letter-spacing: 0.04em;
}
@media (max-width: 39.9375em) {
  .hero-section .row .col-1 h1 {
    font-size: 1.5rem;
  }
}
.hero-section .row .col-1 p {
  font-size: 1.5rem;
  margin: 0;
  font-weight: 500;
}
@media (max-width: 39.9375em) {
  .hero-section .row .col-1 p {
    font-size: 1.2rem;
  }
}
@media (max-width: 39.9375em) {
  .hero-section .row .col-1 {
    text-align: center;
    margin: auto;
    padding-top: 5rem;
  }
}
@media (max-width: ) {
  .hero-section {
    position: relative;
  }
}

.col-2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 4rem;
}
.col-2 .contact-container {
  text-align: center;
  background-color: var(--primary-two);
  height: 640px;
  width: 480px;
  padding: 24px 0;
  color: var(--headings);
}
.col-2 .contact-container h1 {
  font-size: 2.5rem;
  font-weight: 500;
  margin-bottom: 1px;
}
.col-2 .contact-container p {
  margin: 2px 0 37px 0;
  font-weight: 200;
  font-size: 0.9375rem;
}
.col-2 .contact-container .contact {
  display: flex;
  flex-direction: column;
  gap: 25px;
  align-items: center;
}
.col-2 .contact-container .contact input {
  width: 286px;
  height: 2.5rem;
  font-family: var(--montserrat);
  border: none;
  padding: 0.5rem;
  font-family: var(--montserrat);
  font-size: 1rem;
  border: none;
  outline: none;
}
.col-2 .contact-container .contact textarea {
  resize: none;
  width: 286px;
  height: 117px;
  font-family: var(--montserrat);
  font-size: 1rem;
  outline: none;
}
.col-2 .contact-container .contact .send-btn {
  width: 286px;
  height: 2.5rem;
  text-align: center;
  color: var(--headings);
  font-family: var(--montserrat);
  background-color: var(--accent);
  font-size: 1rem;
  font-weight: 600;
  text-transform: uppercase;
  border: none;
  cursor: pointer;
}
.col-2 .contact-container .contact .send-btn:hover {
  background-color: var(--button);
  transition: background-color 0.3s ease-in-out;
}
@media (max-width: 39.9375em) {
  .col-2 .contact-container {
    position: absolute;
    top: 40%;
    transform: translateX(-57%);
    width: 360px;
    height: 560px;
  }
}

.work-showcase {
  text-align: center;
  background-color: var(--primary);
  color: var(--headings);
  padding: 3rem;
}
.work-showcase h1 {
  font-size: 2rem;
  font-weight: 600;
  text-transform: uppercase;
}
.work-showcase .gallery-grid {
  --gap: 16px;
  --num-cols: 2;
  --row-height: 330px;
  box-sizing: border-box;
  padding: 0 32rem;
  display: grid;
  grid-template-columns: repeat(var(--num-cols), 1fr);
  grid-auto-rows: var(--row-height);
  gap: var(--gap);
}
.work-showcase .gallery-grid :hover {
  transform: scale(1.1);
  transition: 0.3s ease-in-out;
  transform: translateY(-5px);
}
@media (max-width: 87.4375em) {
  .work-showcase .gallery-grid {
    padding: 0 5rem;
  }
}
@media (max-width: 39.9375em) {
  .work-showcase .gallery-grid {
    grid-template-columns: repeat(1, 1fr);
    padding: 0;
  }
}
.work-showcase .gallery-grid > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.about {
  display: flex;
  margin: 7.2rem 1rem;
  padding: 1rem;
  justify-content: space-around;
  align-items: center;
  position: relative;
  height: 70vh;
}
@media (max-width: 39.9375em) {
  .about {
    flex-direction: column;
  }
}
.about .img-box {
  margin: 0 auto;
}
.about .img-box img {
  display: block;
}
@media (max-width: 87.4375em) {
  .about .img-box img {
    width: 480px;
    height: 360px;
  }
}
@media (max-width: 39.9375em) {
  .about .img-box img {
    width: 360px;
    height: 240px;
  }
}
.about .safety-text {
  position: absolute;
  background-color: #fff;
  z-index: 1;
  top: 60%;
  left: 30%;
  padding: 66px;
  color: var(--text);
}
@media (max-width: 87.4375em) {
  .about .safety-text {
    left: 20%;
    top: 55%;
  }
}
@media (max-width: 39.9375em) {
  .about .safety-text {
    position: static;
    padding: 0;
    text-align: center;
  }
}

.address-container {
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin: 12.65rem;
}
.address-container .address-text {
  color: var(--text);
  padding: 1rem;
}
.address-container .address-text h2 {
  font-size: 2.5rem;
  text-align: center;
}
.address-container .address-text p {
  font-size: 1.5rem;
  line-height: 2.1rem;
  margin-top: 0.5rem;
}
.address-container iframe {
  width: 600px;
  height: 450px;
  padding: 1rem;
}
@media (max-width: 39.9375em) {
  .address-container iframe {
    width: 95vw;
  }
}
@media (max-width: 63.9375em) {
  .address-container {
    flex-direction: column;
  }
}
@media (max-width: 39.9375em) {
  .address-container {
    margin: 5rem 0;
  }
}

footer {
  background-color: var(--primary);
  color: var(--headings);
  display: flex;
  justify-content: space-around;
  align-items: center;
}
footer img {
  max-width: 15.25rem;
}
@media (max-width: 39.9375em) {
  footer {
    flex-direction: column;
  }
}